/*     (  ) */
.home-search-bar {
    width: 90%;
    max-width: 1000px;
    margin: 20px auto;
    
    padding: 20px;
    background: rgba(255, 255, 255, 0.85); /*   */
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    backdrop-filter: blur(10px); /*   */
}

/*     */
.home-search-bar .search-field{
    flex: 1;
    min-width: 180px;
    padding: 12px;
    border: 1px solid rgba(200, 200, 200, 0.5); /*   */
    border-radius: 8px;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.7); /*     */
}
.home-search-bar .location-select {
    flex: 0.4;
    min-width: 180px;
    padding: 12px;
    color: black;
    border: 1px solid rgba(200, 200, 200, 0.5); /*   */
    border-radius: 8px;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.7); /*     */
}
/**/
.home-search-bar .people-select {
    flex: 0.4;
    min-width: 180px;
    padding: 12px;
    color: black;
    border: 1px solid rgba(200, 200, 200, 0.5); /*   */
    border-radius: 8px;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.7); /*     */
}
/*     */
.home-search-bar .date-container {
    flex: 1;
    min-width: 180px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
}

.home-search-bar .date-label {
    font-size: 14px;
    font-weight: bold;
    color: #333;
    margin-left: 5px;
}

.home-search-bar .date-field {
    width: 100%;
    padding: 12px;
    color: black;
    border: 1px solid rgba(200, 200, 200, 0.5);
    border-radius: 8px;
    font-size: 16px;
    text-align: center;
    background: rgba(255, 255, 255, 0.7);
}

/*    */
.home-search-bar .search-btn {
    flex: 0.8;
    min-width: 130px;
    margin-top: 37px;
    padding: 12px 20px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    background-color: rgba(255, 107, 107, 0.9); /*   */
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
}

.home-search-bar .search-btn:hover {
    background-color: rgba(255, 77, 77, 0.9);
}
