/******* buslist  **************/
.buslist {
    background-color: #fff; /*  */
    padding: 20px; /*   */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /*   */
    border-radius: 8px; /*   */
    margin-bottom: 30px; /*   */
  }
  
  .buslist .sidebar-wrap {
    padding: 20px;
  }
  
  .buslist .heading {
    text-align: center;
    font-size: 24px;/*     */
  
  }

/*     */
.buscontainer {
    max-width: 1200px;
    margin: 0 auto;
}
/*   */
.businformation {
    width: 100%;
    margin: 20px 0;
    border-collapse: collapse;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.businformation thead {
    background-color: #2d3e50;
    color: white;
}

.businformation th,
.businformation td {
    padding: 15px;
    text-align: center;
    border-bottom: 1px solid #ddd;
}
/***********************************************/
/*Busreservation*/
/*json   */
.busdetail{
 display: flex;
 justify-Content: center;
 margin-top: 20px; 
}
.busdetail table {
  border-collapse: collapse;
  width: 80%;
}
.busdetail th {

  border: 1px solid #d3d3d3;
  padding: 8px;
  text-align: center;
}
.busdetail td{
  border: 1px solid #d3d3d3;
  padding: 8px;
  text-align: center;
}

/*      */
.seat {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: #e0e0e0;
  text-align: center;
  line-height: 50px;
  margin: 5px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}
.seat-wrapper{
  width: 300px;
  margin: auto;
}
.seat-row {
  display: flex;
  justify-content: space-between; /*      */
  margin-bottom: 10px; /*      */
}

.seat-pair {
  display: flex; /*        */
  gap: 5px; /*    */
  margin-right: 10px; /*  (A1A2, A3A4)    */
}


.seat.clicked {
  background-color: #5cb85c; /*     */
  color: white; /*     */
}