.weather-container {
    display: flex;
    flex-direction: column; /*    */
    align-items: center;
    width: 80%;
    max-width: 2000px;
    margin: 50px auto;
    padding: 30px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
}

.weather-header {
    font-size: 3.5em;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px; /*  margin   */
}

.weather-select {
    width: 20%;
    padding: 12px;
    margin-bottom: 20px; /*  margin   */
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1.1em;
    background-color: #fafafa;
    transition: background-color 0.3s;
}

.weather-select:focus {
    background-color: #e3f2fd;
}

.weather-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    gap: 20px;
    margin-top: 30px;
    align-items: flex-start;
    flex-wrap: wrap;
}

.weather-info,
.weather-forecast-info {
    width: 48%;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.weather-info {
    display: flex; /* Flexbox  */
    flex-direction: row; /*    */
    justify-content: space-between; /*     */
    align-items: center; /*     */
}

.weather-forecast-info {
    display: flex; /* Flexbox  */
    flex-direction: row; /*    */
    justify-content: space-around; /*       */
    width: 100%; /*      */
}
.weather-info img {
    width: 100px; /*    */
    height: 100px; /*    */
    margin-left: 20px; /*    */
}
.weather-forecast-info ul {
    list-style: none; /*    */
    padding: 0; /*    */
    margin: 0; /*    */
    display: flex; /* ul  Flexbox  */
    flex-direction: row; /*    */
    width: 100%; /*      */
}

.weather-forecast-info li {
    /*     */
    text-align: center; /*    */
    flex: 1; /*       */
}
.weather-forecast-info li img {
    width: 50px; /*    */
    height: 50px; /*    */
}
.weekly-forecast-title {
    font-family: 'Arial', sans-serif; /*   */
    font-size: 1.5em; /*   */
    font-weight: bold; /*   */
    color: #140707; /*   */
    text-align: left; /*   */
    background-color: #e0f2f7; /*   */
    padding: 10px; /*   */
    border-radius: 5px; /*    */
    margin-bottom: 20px; /*    */
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); /*   */
}

.weekly-forecast-title i { /*   */
    margin-right: 10px;
    animation: weather-icon-animate 1s linear infinite; /*   */
}



@media (max-width: 768px) {
    .weather-container {
        width: 90%;
        flex-direction: column;
    }

    .weather-content {
        flex-direction: column;
    }

    .weather-info,
    .weather-forecast-info {
        width: 100%;
    }
}