/* MyBookshelf.css */
.bookshelf .addDiary {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;  /*   */
    height: 200px;  /*   */
    border: 2px solid #ccc;
    border-radius: 10px; /*   */
    background-color: #fcf8ed; /*   */
    color: #333;  /*   */
    font-size: 36px;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);  /*   */
    position: relative;
    overflow: hidden;
    transition: transform 0.2s ease-in-out;
  }
  
  .bookshelf  .addDiary:hover {
    background-color: #ce9a57;  /* hover    */
    color: white;  /* hover    */
    transform: scale(1.05);  /* hover     */
  }
  
  .bookshelf  .addDiary::before {
    content: "+";  /*  "+"   */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 40px;
    font-weight: bold;
    color: #333;
    z-index: 10;
  }
  
  .bookshelf .addDiary:hover::before {
    color: white;  /* hover  "+"   */
  }
  
  .bookshelf .addDiary::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to top left, #ff7e5f, #feb47b); /*     */
    opacity: 0.1; /*      */
    z-index: 0;
  }

/*   */
.bookshelf .bookCover {
    position: relative;
    width: 140px;
    height: 200px;
    overflow: hidden;
    border-radius: 10px;
    background-color: #f0f0f0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  /*    */
  .bookshelf .shelfImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  /*   */
  .bookshelf .bookTitle {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    color: white;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 5px;
    border-radius: 5px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.3s, transform 0.3s;
  }
  
  /* hover    */
  .bookshelf .shelfItem:hover .bookTitle {
    opacity: 1;
    transform: translateY(0);
  }

